.shoppingCart__Cart__1Yh-1 {
    position: fixed;
    top: 0px;
    right: -400px;
    height: 100%;
    width: 400px;
    background-color: black;
    transition: all 0.3s;
    /* display: flex;
    flex-direction: column; */
}

.shoppingCart__Opened__1B_aX {
    right: 0px;
}


.shoppingCart__CartButtonClosed__3wOQy {
    position: absolute;
    top: 0px;
    left: -60px;
    height: 60px;
    width: 60px;
    background-image: url(data:image/png;base64,);
    background-repeat: no-repeat;
    background-position: center;
    background-color: black;
    cursor: pointer;
    
}

.shoppingCart__CartButtonOpened__3qEpt {
    height: 50px;
    width: 50px;
    background-image: url(data:image/png;base64,);
    background-repeat: no-repeat;
    background-position: center;
    background-color: black;
    cursor: pointer;
}

.shoppingCart__AmountIcon__1NEvq {
    background-color: rgb(255, 255, 0);
    color: black;
    font-size: 4px;
    position: absolute;
    bottom: 5px;
    right: 10px;
    text-align: center;
    display: inline-block;
    width: 19px;
    height: 19px;
    border-radius: 50%;
}

@media (max-width: 640px) {
    .shoppingCart__Opened__1B_aX {
        right: 0px;
        width: 100%;
    }
    
}
  
